
import React, { useState } from 'react';
import { Button, Popover, PopoverHeader, PopoverBody } from 'reactstrap';




const PopEvenement = (props) => {
    const [popoverOpen, setPopoverOpen] = useState(false);

    const toggle = () => setPopoverOpen(!popoverOpen);

    return (
        <div className="evenementCard">
            <Button id="Popover1" type="button" className="btn-evenement">
                + d'infos
            </Button>
            <Popover placement="bottom" isOpen={popoverOpen} target="Popover1" toggle={toggle} trigger="focus" >

                <PopoverHeader>Marchés Orléannais</PopoverHeader>
                <PopoverBody className="popEvenement d-flex flex-column align-items-center justify-content-center">
                    <img className="mt-1" src={process.env.PUBLIC_URL + '/evenements/marche.jfif'} alt="" />
                    <h1> MARCHES HEBDOMADAIRES D'HIVER</h1>
                    <h2>Du 29/02/2021 Au 31/02/2021</h2>
                    <h3> Place du marché couvert</h3>
                    <div className="d-flex ">
                        <div className="d-flex flex-column m-5 ">
                            <p>Horaires: de 8h à 17h du lundi au vendredi</p>
                            <p>Horaires: de 8h à 16h Samedi et Dimanche</p>
                            <p>Tarifs: Entrées libres</p>
                        </div>
                        <div className="d-flex flex-column m-5 ">

                            <p><a href="/Sortons_ensemble">Créer une sortie pour cet évènement!</a></p>
                            <p> Lien évenement</p>
                        </div>
                        <div className="d-flex flex-column m-5 ">
                            <p>Accès par carte </p>
                            <img className="mt-1" src={process.env.PUBLIC_URL + '/evenements/carteMarche.png'} alt="" height="200px" width="400px" />

                        </div>
                    </div>
                    <p>Contact évenement</p>
                    <h4>Marchés et foires</h4>

                </PopoverBody>
            </Popover>
        </div>
    );
}





export default PopEvenement;



