
import React from 'react'
import PopEvenement from './popEvenement';

function Evenement() {
    return (
        <div className="evenement d-flex flex-column align-items-center m-3">
            <img className="mt-1" src={process.env.PUBLIC_URL + '/evenements/marche.jfif'} alt="" width="192px" height="192px" />
            <h1> MARCHES HEBDOMADAIRES D'HIVER</h1>
            <h2>Du 29/02/2021 Au 31/02/2021</h2>
            <h3> Place du marché couvert</h3>
            <h4>Marchés et foires</h4>
            <PopEvenement />

        </div>
    )
}

export default Evenement;

